/**
 * Block Styles
 *
 * The following styles get applied both on the front of your site
 * and in the editor.
 */
/**
 * Block
 *
 * The styles for the Block.
 */
.example-block {
  align-items: flex-start;
  background-color: #e2dccd;
  display: flex; }
  .example-block__figure {
    height: 200px;
    margin: 1em;
    margin-right: 1em;
    width: 200px !important; }
  .example-block__title {
    margin-top: .1em; }
  .example-block__body {
    flex: 1;
    margin-right: 1em; }
  .example-block.is-style-image-right {
    flex-direction: row-reverse; }
    .example-block.is-style-image-right .example-block__body {
      margin-left: 1em;
      margin-right: 0; }
